import { Component, OnInit, ViewChild } from '@angular/core';
import { SearchService, SermonFilter } from './search-sermon.service';
import { Subject } from 'rxjs';
import { Sermon } from '@shared/models';
import { SermonService, PagerService, UtilsService } from '@shared/services';
import { IMyOptions, ModalDirective } from 'ng-uikit-pro-standard';
import { BsLocaleService } from 'ngx-bootstrap/datepicker';
import * as _moment from 'moment';

@Component({
  selector: 'app-sermon-archive',
  templateUrl: './sermon-archive.component.html',
  styleUrls: ['./sermon-archive.component.scss'],
  providers: [BsLocaleService]
})
export class SermonArchiveComponent implements OnInit {
  currentSermon: Sermon;
  sermons: Sermon[] = [];

  pager: any = {};
  pagedItems: Sermon[];
  searchText: string;
  dateFilter: string;
  searchTerm$ = new Subject<string>();
  public myDatePickerOptions: IMyOptions = {
    sunHighlight: true,
    markWeekends: { marked: true, color: 'red' },
    minYear: 1990
  };

  sermonFilter: SermonFilter;
  showFilter = false;
  selectedSpeakers = [];
  selectedYears = [];
  dateFilterSelected = [];
  optionsYears = [];
  optionsSpeakers = [
    { value: '1', label: 'Pasteur Richard Diyoka' },
    { value: '2', label: 'Fr. Dauphin Elalanga' },
    { value: '3', label: 'Fr. Kasiala' },
    { value: '4', label: 'Autre Ministres' }
  ];

  @ViewChild('videoModal') videoPlayer: ModalDirective;

  constructor(
    private service: SermonService,
    private pagerService: PagerService,
    private searchService: SearchService,
    private utilService: UtilsService,
    localeService: BsLocaleService
  ) {
    localeService.use('fr');
  }

  ngOnInit() {
    this.optionsYears = this.utilService.generateYearItem();
    this.service.getSermons().subscribe(items => {
      this.sermons = items;
      this.setPage(1);
    });
    this.sermonFilter = {
      speakers: [],
      dates: [],
      years: []
    };
  }

  loadItems(items: Sermon[]) {
    this.sermons = items;
    this.setPage(1);
  }

  play(item: Sermon) {
    this.currentSermon = null;
    this.currentSermon = item;
    this.videoPlayer.show();
  }

  download(item: Sermon) {
    alert(item.title);
  }

  resetFilter() {
    this.dateFilterSelected = null;
    this.selectedSpeakers = [];
    this.selectedYears = [];
    this.showFilter = false;
  }

  research() {
    this.sermonFilter.speakers = this.optionsSpeakers
      .filter(x => this.selectedSpeakers.includes(x.value))
      .map(y => y.label);
    this.sermonFilter.dates = this.dateFilterSelected
      ? this.dateFilterSelected.map(x => _moment(x).format('L'))
      : [];
    this.sermonFilter.years = this.selectedYears.map(y => +y);

    this.searchService.researchSermons(this.sermonFilter).subscribe(items => {
      this.sermons = items;
      this.setPage(1);
    });
    this.resetFilter();
  }

  search(event?) {
    if (!event) {
      this.searchService.searchSermons(this.searchText).subscribe(items => {
        this.sermons = items;
        this.setPage(1);
      });
    } else {
      if (event.key === 'Enter') {
        this.searchService
          .searchSermons(event.target.value)
          .subscribe(items => {
            this.sermons = items;
            this.setPage(1);
          });

        event.preventDefault();
      }
    }
  }

  sortSermons(sorteKey) {
    this.pagedItems = this.service.sortByKey(this.pagedItems, sorteKey);
  }

  setPage(page: number) {
    this.pager = this.pagerService.getPager(this.sermons.length, page, 9);
    this.pagedItems = this.sermons.slice(
      this.pager.startIndex,
      this.pager.endIndex + 1
    );
    this.currentSermon = this.pagedItems[0];
  }
}
