import { Component, OnInit, HostListener } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  menuTitleVisible = false;
  menus: any = [];
  menuOpen = false;
  isDesktop = true;
  iconsSelect: Array<any>;
  currentLng: string;

  constructor(public translate: TranslateService) {
    this.currentLng = translate.currentLang;
  }

  @HostListener('window:scroll', ['$event'])
  onWindowScroll() {
    if (window.innerWidth < 800) {
      return;
    }
    const header = document.getElementById('myHeader');
    if (window.pageYOffset > 180) {
      header.classList.add('sticky');
      this.menuTitleVisible = true;
    } else {
      header.classList.remove('sticky');
      this.menuTitleVisible = false;
    }
  }

  ngOnInit() {
    if (window.innerWidth < 930) {
      this.isDesktop = false;
    } else {
      this.isDesktop = true;
    }

    this.iconsSelect = [
      {
        value: 'en',
        label: 'En',
        icon: '/assets/images/en.png'
      },
      {
        value: 'fr',
        label: 'Fr',
        icon: '/assets/images/fr.png'
      }
    ];
  }

  optionChanged(value) {
    this.currentLng = value;
    this.translate.use(value);
  }
}
