import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Sermon, DATA_SEMONS } from '../models';
import { takeLast, map, last } from 'rxjs/operators';

@Injectable()
export class SermonService {
  constructor() {}

  getSermons(): Observable<Sermon[]> {
    return of(DATA_SEMONS).pipe(
      map(items =>
        items.sort(
          (a, b) => new Date(b.date).getTime() - new Date(a.date).getTime()
        )
      ),
      map(item => item as Sermon[])
    );
  }

  getLastTwoSermons(): Observable<Sermon[]> {
    return of(DATA_SEMONS).pipe(
      map((items: Sermon[]) => {
        return this.sortByKey(items, 'date');
      }),
      map((items: Sermon[]) => {
        return items.slice(0, 2);
      })
      // map((items: Sermon[]) => {
      //   return items.sort((a, b) => (a.id < b.id ? 1 : 0));
      // })
    );
  }

  sortByKey(items: Sermon[], key) {
    return items.sort((a, b) => {
      let x = a[key];
      let y = b[key];

      if (typeof x === 'string') {
        x = ('' + x).toLowerCase();
      }
      if (typeof y === 'string') {
        y = ('' + y).toLowerCase();
      }

      return x < y ? -1 : x > y ? 1 : 0;
    });
  }

  getQuote() {
    const quotes = [
      {
        description: 'home.quote.item1.description',
        source: 'home.quote.item1.source'
      },
      {
        description: 'home.quote.item2.description',
        source: 'home.quote.item2.source'
      },
      {
        description: 'home.quote.item3.description',
        source: 'home.quote.item3.source'
      },
      {
        description: 'home.quote.item4.description',
        source: 'home.quote.item4.source'
      }
    ];
    return quotes[Math.floor(Math.random() * quotes.length)];
  }
}
