import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalDirective } from 'ng-uikit-pro-standard';

@Component({
  selector: 'app-live-streaming',
  templateUrl: './live-streaming.component.html',
  styleUrls: ['./live-streaming.component.scss'],
  providers: [ModalDirective]
})
export class LiveStreamingComponent implements OnInit {
  title: string;
  isPlaying = false;
  @ViewChild('frame') frameVideo: ModalDirective;
  constructor() {}

  ngOnInit() {}

  goLiveFr() {
    this.title = 'Francais';
    this.isPlaying = true;
    this.frameVideo.show();
  }

  goLiveEn() {
    this.title = 'English';
    this.isPlaying = true;
    this.frameVideo.show();
  }

  goLivePg() {
    this.title = 'Portuguese';
    this.isPlaying = true;
    this.frameVideo.show();
  }


  onClosed(event: any) {
    this.isPlaying = false;
  }
}
