import { Injectable } from '@angular/core';
import { SermonService } from '@shared/services';
import { map } from 'rxjs/operators';

export interface SermonFilter {
  speakers: Array<string>;
  dates: Array<string>;
  years: Array<number>;
}

@Injectable()
export class SearchService {
  baseUrl = 'https://api.cdnjs.com/libraries';
  queryUrl = '?search=';

  constructor(private service: SermonService) {}

  //   search(terms: Observable<string>) {
  //     return terms.pipe(
  //         debounceTime(400),
  //         distinctUntilChanged(),
  //         switchMap(term => this.searchEntries(term))
  //     );
  //   }
  researchSermons(filter: SermonFilter) {
    return this.service
      .getSermons()
      .pipe(
        map(item =>
          item.filter(
            x =>
              filter.speakers.map(u => u.toLowerCase()).includes(x.preacher.toLowerCase()) ||
              filter.dates.includes(x.date) ||
              filter.years.includes(new Date(x.date).getFullYear())
          )
        )
      );
  }
  searchSermons(term) {
    const filterItem = term ? term.toLowerCase() : '';
    return this.service
      .getSermons()
      .pipe(
        map(item =>
          item.filter(
            x =>
              x.title.toLowerCase().includes(filterItem) ||
              x.preacher.toLowerCase().includes(filterItem) ||
              x.date.includes(filterItem) ||
              x.location.toLowerCase().includes(filterItem)
          )
        )
      );
  }
}
