import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
@Pipe({
  name: 'safeVimeo'
})
export class SafeVimeoPipe implements PipeTransform {
  constructor(protected sanitizer: DomSanitizer) {}
  public transform(value: any): SafeResourceUrl {
    if (!value) {
      return '';
    }
    const uri = `https://player.vimeo.com/video/${value}?color=f5f8f9&title=0&byline=0&portrait=0`;
    return this.sanitizer.bypassSecurityTrustResourceUrl(uri);
  }
}
