import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { MDBBootstrapModule, ModalDirective } from 'angular-bootstrap-md';
import { UIKIT_BASE, UIKIT } from '@shared/ui/ui-kit.asset';
import { AccordionModule, MDBBootstrapModulesPro } from 'ng-uikit-pro-standard';
// ModalModule, TooltipModule, PopoverModule, ButtonsModule

const SVC = [ModalDirective];

@NgModule({
  imports: [
    CommonModule,
    ...UIKIT_BASE,
    // SidenavModule,
    // SelectModule,
    ...UIKIT,
    AccordionModule,
    MDBBootstrapModulesPro.forRoot()
  ],
  providers: [...SVC],
  exports: [MDBBootstrapModule]
})
export class AppBootstrapModule {}
