import { Observable } from 'rxjs';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SermonService } from '@shared/services';
import { Sermon } from '@shared/models';
import { ModalDirective } from 'ng-uikit-pro-standard';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  recentSermon$: Observable<Sermon[]>;
  videoId: string;
  @ViewChild('frame') frameVideo: ModalDirective;

  quote = this.sermonService.getQuote();

  constructor(private sermonService: SermonService) {}

  ngOnInit() {
    this.recentSermon$ = this.sermonService.getLastTwoSermons();
  }

  playVideo(videoId: string) {
    this.videoId = videoId;
    this.frameVideo.show();
  }

  onClosed(event: any) {
    this.videoId = null;
  }
}
